<template>
    <div>
        <h2></h2>
        <div class="checkorder">
            <div class="flight">
                <div class="GO">
                    <p class="go"></p>
                    <p>:{{From.Departure}}</p>
                    <p>:{{From.Arrival}}</p>
                    <p>:{{From.DepartureTime}}</p>
                    <p>:{{From.ArrivalTime}}</p>
                </div>
                <div class="BACK">
                    <p class="back"></p>
                    <p>:{{To.Departure}}</p>
                    <p>:{{To.Arrival}}</p>
                    <p>:{{To.DepartureTime}}</p>
                    <p>:{{To.ArrivalTime}}</p>
                </div>
            </div>
            <div class="passenger">
                <div class="title"></div>
                <div class="firstRow">
                    <span>:{{ passenger.title | title() }}</span>
                </div>
                <div class="secondRow">
                    <span>:{{passenger.lastName}}</span>
                    <span>:{{passenger.name}}</span>
                </div>

                <div class="threeRow">
                    <span>:{{passenger.brith | brith() }}</span>
                </div>

                <div class="fourRow">
                    <span>:{{passenger.country | country() }}</span>
                </div>

                <div class="fourRow">
                    <span>:{{passenger.passport}}</span>
                </div>
            </div>
            <div class="total">
                 : {{totalPrice}}
            </div>
        </div>
    </div>
</template>

<script>

function zero(date){
    return date < 10 ? "0"+date : date; 
}

export default {
    data(){
        return{
            From:{},
            To:{},
            passenger:{},
            totalPrice:""
        }
    },
    mounted(){
        this.From = this.$store.state.cart.From;
        this.To = this.$store.state.cart.To;
        this.passenger = this.$store.state.passengerData;
        this.totalPrice = this.$store.state.cart.totalPrice;
    },
    filters:{
        title(title){
            if(title === "Mr"){
                return ""
            }else{
                return ""
            }
        },
        brith(brith){
            let date = new Date(brith)
            let Y = date.getFullYear();
            let M = date.getMonth()+1;
            let D = date.getDate();
            return ` ${Y}  ${zero(M)}  ${zero(D)} `
        },
        country(country){
            switch(country){
                case 'taiwan':
                    return ''
                break
                case 'japan':
                    return ''
                break
                case 'korea':
                    return ''
                break
                case 'singapore':
                    return ''
                break
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/styles/checkorder.scss';
</style>

