
<template>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="title">
            <el-select v-model="ruleForm.title" placeholder="">
            <el-option label="" value="Mr"></el-option>
            <el-option label="" value="Mrs"></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="" prop="name">
            <el-input v-model="ruleForm.name" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="lastName">
            <el-input v-model="ruleForm.lastName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="gender">
            <el-select v-model="ruleForm.gender" placeholder="">
            <el-option label="" value="man"></el-option>
            <el-option label="" value="female"></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="" prop="brith">
            <el-date-picker type="date" placeholder="" v-model="ruleForm.brith"></el-date-picker>
        </el-form-item>

        <el-form-item label="" prop="country">
            <el-select v-model="ruleForm.country" placeholder="">
            <el-option label="" value="taiwan">
                <img src="../assets/img/taiwan.svg" alt="" width="20">
                <span></span>
            </el-option>
            <el-option label="" value="japan">
                <img src="../assets/img/japan.svg" alt="" width="20">
                <span></span>
            </el-option>
            <el-option label="" value="korea">
                <img src="../assets/img/south-korea.svg" alt="" width="20">
                <span></span>
            </el-option>
            <el-option label="" value="singapore">
                <img src="../assets/img/singapore.svg" alt="" width="20">
                <span></span>
            </el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="" prop="passport">
            <el-input v-model.number="ruleForm.passport"></el-input>
        </el-form-item>
        
        <el-form-item v-if=closesumbit>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
    </el-form>
</template>
<script>
  export default {
      props:['closesumbit'],
    data() {
      return {
        ruleForm: {
            title:"",
            name:"",
            brith:"",
            lastName:"",
            gender:"",
            country:"",
            passport:""
        },
        toRouterLink:false,
        rules: {
            title:{ required: true, message: '', trigger: 'blur' },
            name:{ required: true, message: '', trigger: 'blur' },
            lastName:{ required: true, message: '', trigger: 'blur'},
            gender:{ required: true, message: '', trigger: 'blur'},
            brith:{ required: true, message: '', trigger: 'blur'},
            country:{ required: true, message: '', trigger: 'blur'},
            passport:[
                { required: true, message: '', trigger: 'blur'},
                { type: 'number', message: '', trigger: 'blur'}
            ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$store.dispatch('addpassengerData', this.ruleForm);
            if(this.toRouterLink) this.$router.push(this.routerName)
            return true;
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        
      }
    },
    mounted(){
        this.$bus.$on('verification', (router)=>{
            this.toRouterLink = true
            this.routerName = router
            this.submitForm('ruleForm');
        })
    }
  }
</script>